.page {

  &-main {
    position: relative;
    min-height: 100vh;

    &::before {
      content: '';
      width: 50%;
      max-width: 550px;
      padding-bottom: 50%;
      height: 0;
      position: fixed;
      top: 0;
      right: 0;
      background-image: url(../img/home/canto-direito.png);
      background-position: top right;
      background-repeat: no-repeat;
      background-size: contain; }


    &::after {
      content: '';
      width: 50%;
      max-width: 550px;
      padding-bottom: 50%;
      height: 0;
      position: fixed;
      bottom: 0;
      left: 0;
      background-image: url(../img/home/canto-esquerdo.png);
      background-position: bottom left;
      background-repeat: no-repeat;
      background-size: contain; } }

  &-container {
    padding: 75px;
    position: relative;
    z-index: 8; }

  &-row {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap; }

  &-article {
    width: calc(100% - 205px);
    padding-right: 65px;

    @include mediaMaxWidth(1200px) {
      width: 100%; } }

  &-sidebar {
    width: 205px;
    border-left: 8px solid #D9D9D9;

    @include mediaMaxWidth(1200px) {
      width: 100%;
      border-top: 8px solid #D9D9D9;
      border-left: 0;
      margin-top: 80px; } }

  &-header {
    padding-bottom: .5em;

    h1 {
      font-size: 46px;
      line-height: 1.5em;
      padding-bottom: .5em;

      @include mediaMaxWidth(768px) {
        font-size: 30px; }

      @include mediaMaxWidth(550px) {
        font-size: 25px; } }

    h2 {
      font-size: 35px;
      line-height: 1.5em;
      padding-bottom: .5em;

      @include mediaMaxWidth(768px) {
        font-size: 25px; }

      @include mediaMaxWidth(550px) {
        font-size: 20px; } }
    h3 {
      font-size: 25px;
      line-height: 1.5em;
      padding-bottom: .5em;

      @include mediaMaxWidth(768px) {
        font-size: 20px; }

      @include mediaMaxWidth(550px) {
        font-size: 18px; } } }


  &-content {
    font-size: 16px;
    line-height: 1.5em;

    .row-iframer {
      display: flex;
      flex-wrap: wrap;
      margin-left: -20px;
      margin-right: -20px;
      width: calc(100% + 40px);

      .colomne-iframer {
        padding: 20px;
        width: 50%;

        @include mediaMaxWidth(768px) {
          width: 100%; } } }

    img {
      height: auto; } }

  &-prendre_contact {
    background-color: rgba(255,255,255,.5); } }
